import React, { Component } from 'react';
import Product from './Product';
import Filters from './Filters';
import NotFound from '../assets/NotFound';

class App extends Component{
  constructor(props){
    super(props);
    this.state = {
      showAllProducts: '',
      productsFounds: [],
    }
    this.displayProducts = this.displayProducts.bind(this);
  }

  componentDidMount(){
    this.setState({showAllProducts: 1});
  }

  displayProducts(value){
    this.setState({showAllProducts: value});
  }

  render(){
    return(
      <div className="container">
        <aside className="edit-products">
          <Filters handleDisplay={this.displayProducts} any={this.productsFounds}/>
        </aside>
        <section className="products">
          <Product
          displayProducts={this.state.showAllProducts}
          NotFoundProducts={<NotFound text="Ninguna casilla ha sido marcada..."/> }
          maya={this.state.productsFounds} />
        </section>
      </div>
    )
  }
}

export default App;