import React, { Component } from 'react';
import Icon from '../assets/Icon';
import './css/product.css';
import celulares from '../../json/celulares';

class Product extends Component{
  state = {
      celulares,
      productFound: [],
      inputsChecked: []
  }

  render(){

    const checkboxesChecked = document.querySelectorAll('input[type="checkbox"]:checked'), products = document.querySelectorAll(".product");

    this.state.celulares.filter(el =>{
      
      if (el.modelo.indexOf(this.state.inputsChecked) !== 1){
        return this.state.maya
      }
         
      //   for (let p of products){
      //       let x = p.getElementsByTagName('h3')[0].textContent;
      //       for (let input of checkboxesChecked){
      //         if (x.toLowerCase().indexOf(input.id.toLowerCase()) !== -1){
      //           return p.style.display = "block";
      //         } else {
      //           return p.style.display = "none";
      //         }
      //       }
      //     }
      // }
       
      // return el.modelo.indexOf(this.state.inputsChecked) !== 1;
    })
      

    const { displayProducts, NotFoundProducts } = this.props,
    product = celulares.map((el) =>{
      return (
        <div className="product" key={el.id}>
          <img loading="lazy" src={`${process.env.PUBLIC_URL}/${el.imagen}`} alt={el.modelo} title={el.modelo} />
          <h3>{el.modelo}</h3>
          <h4>$/. {el.precio}</h4>

          <div id="quantity">
            <button>
              <Icon iconName="fas fa-plus" iconStyle={{padding: "0"}}/>
            </button>
            
            <div id="count">{el.stock}</div>
            
            <button>
              <Icon iconName="fas fa-minus" iconStyle={{padding: "0"}}/>
            </button>
          </div>

          <button className="add-to-cart"><Icon iconName="fas fa-cart-plus" iconTitle="AÃ±ade un producto al Carrito" iconStyle={{padding: "0"}}/>&nbsp;&nbsp;AÃ±adir al carrito</button>
        </div>
      )
    })
    
    return (
      <React.Fragment>
        {
        displayProducts === 1
        ? product
        : displayProducts === 2
        ? this.props.maya
        : displayProducts === 3
        ? NotFoundProducts
        : null 
        }
      </React.Fragment>
    )
  }
}

export default Product;